.loginPage {
    background-color: #f6f6f6;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loginCard {
    border: 1px solid silver;
    background-color: #fff;
    padding: 20px 35px;
    border-radius: 10px;
}

.loginCard h2 {
    color: rgb(93, 93, 93);
    font-size: 1.3rem;
    margin-bottom: 40px;
    text-align: center;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #0f9d58;
    padding: 2px 10px;
    width: fit-content;
    margin: 0 auto;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.2 ease;
}

.button:hover {
    background: #07713e;
}

.button img {
    width: 30px;
    margin: 10px
}